<template>
  <div class="Filtration">
    <h3>过滤器</h3>
    <p>有时候我们的数据格式并不是我们想要的,这时候可以使用过滤器,过滤数据</p>
    <h3>自定义全局过滤器</h3>
    <p>全局过滤器当然定义在全局下,比如main.js</p>
    <p>金钱过滤:{{ qian | money1 }}</p>
    <pre>{{ guolvdemo }}</pre>
    <h3>自定义局部过滤器</h3>
    <p>局部过滤器只在当前页有效</p>
    <p>金钱过滤:{{ qian | money2 }}</p>
    <pre>{{ jubudemo }}</pre>
  </div>
</template>

<script>
export default {
  data() {
    return {
      qian: 16854,
      guolvdemo: `
//main.js中

/*  自定义全局过滤器 金钱过滤  */
Vue.filter('money1', (value) => {
  return "￥" + Number(value).toFixed(2);
})

//html中
<p>金钱过滤:{{ qian | money }}</p>

//data中
 qian:16854`,
      jubudemo: `
 //局部过滤器 当前页钩子中
  filters: {
    money2(value) {
      return "￥" + Number(value).toFixed(2);
    },
  },

//html中
<p>金钱过滤:{{ qian | money2 }}</p>

//data中
qian: 16854`,
    };
  },
  methods: {},
  mounted() {},
  computed: {},
  //局部过滤器
  filters: {
    money2(value) {
      return "￥" + Number(value).toFixed(2);
    },
  },
};
</script>

